.header {
  position: relative;
  width: 100%;
  /* height: calc(100vh - 200px); */
  display: flex;
}

.header-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.header-title {
  color: #fff;
  font-size: 80px;
  font-family: 'Righteous', cursive;
  margin-bottom: 1rem;
